.contact-us {
  &__info {
    margin: 0 0 24px;
    &--last {
      margin-bottom: 32px;
    }
  }

  &__info-title {
    margin: 0 0 8px;
    font-size: 14px;
    line-height: 27px;
    color: $colorGrey;
  }

  &__info-link {
    color: inherit;
    text-decoration: none;
    transition: 0.3s;
    &:hover {
      color: $colorBlue;
    }
  }

  &__filed {
    margin: 0 0 16px;
    &--last {
      margin-bottom: 32px;
    }
  }
}
